body {
  width: 100%;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-image: url(https://i.ibb.co/zbCwnW2/FondoSVG.png);
  
}

header{
  height: 15vh;
  padding: 0.5rem;
  margin-top: 30px;
  background-color: #66ffcc;
  display: flex;
  align-items: center;
  box-shadow: 0px 0px 5px 0px black;

}
 
 .contenedorImagen{
   margin-left: 4%;
   display: flex;
   flex-direction: row;
   justify-content: left;
   align-items: center;
    width: 90%;
    height: 90%; 
 }

  .logo1{
    display: flex;
    height: 100%;
    
  }

.contenedorMenu{
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: right;
  align-items: center;

}

.MuiMenu-list{
  background-color: aquamarine;
}
  
.MuiMenu-list{
  color: #124d00;
}

.MuiSvgIcon-root {
  color: #124d00;
}

.contenedorBuscador{
  width: 100%;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;

}  

  .buscador1{
    width: 60%;
    height: 40%;
    background-color: rgba(255, 255, 255, 0);
    color:#124d00;
    border-radius: 80px;
    padding: 1%;
    border: none;
    border-top:none;
    outline: none;
    text-align: center;
    font-size: 40px;
    box-shadow:0px 0px 4px 0px rgb(0, 0, 0);
  }

      ::-webkit-input-placeholder { color:#124d00; } 

      :-moz-placeholder { /* Firefox 18- */ color: white; } 
      
      ::-moz-placeholder { /* Firefox 19+ */ color: white; } 
      
      :-ms-input-placeholder { color: white; }


section{
  display: flex;
  height: 82vh;

}

footer{
  display: flex;
  height: 8vh;
  background-color: black;
  color: white;
  justify-content: center;
}


/* -------------- */

@media screen and (max-width: 800px){
  header{
    height: 10vh;
    padding: 0.5rem;
    background-color: #ca0000;
    color: white;
    display: flex;
    align-items: center;
    justify-content: right;
  
  }


  .contenedorImagen{
    width: 10%;
  }



}